import React, { useRef, useState } from 'react';
import {
    type AriaTextFieldProps,
    mergeProps,
    useFocusRing,
    useTextField,
} from 'react-aria';
import { Item } from 'react-stately';
import classNames from 'classnames';

import { Stack } from '../Stack';
import type { DescriptionSeverity } from '../types';
import { Label, Text } from '../Typography';

import Select from './Select';

import styles from './Phone.module.scss';

type AreaCode = {
    code: string;
    country: string;
};

export interface PhoneProps extends Omit<AriaTextFieldProps, 'errorMessage'> {
    errorMessage?: string;
    descriptionSeverity?: DescriptionSeverity;
    labelSuffix?: string;
    tooltipTrigger?: React.ReactNode;
    selectItems: AreaCode[];
    selectName?: string;
    selectPlaceholder?: string;
    selectDefaultSelectedKey?: string;
}

const Phone = (props: PhoneProps) => {
    const textfieldRef = useRef(null);

    const [isSelectFocused, setIsSelectFocused] = useState(false);

    const {
        labelProps,
        inputProps,
        isInvalid,
        errorMessageProps,
        descriptionProps,
        validationErrors,
    } = useTextField(props, textfieldRef);
    const { isFocusVisible, focusProps: textfieldFocusProps } = useFocusRing();

    const {
        label,
        labelSuffix,
        tooltipTrigger,
        description,
        validationBehavior,
        errorMessage: customErrorMessage,
        descriptionSeverity = 'regular',
        selectItems,
        selectName,
        selectPlaceholder,
        selectDefaultSelectedKey,
    } = props;

    const errorMessage =
        validationBehavior === 'native'
            ? validationErrors.join(' ')
            : customErrorMessage;

    const hasMessages = errorMessage || description;

    const baseClassList = classNames(styles.base, {
        [styles['base--focused']]: isFocusVisible,
        [styles['base--focus-within']]: isSelectFocused,
        [styles['base--invalid']]: isInvalid,
    });

    const descriptionClassList = classNames({
        [styles['messages__description--regular']]:
            descriptionSeverity === 'regular',
        [styles['messages__description--warning']]:
            descriptionSeverity === 'warning',
    });

    return (
        <Stack>
            {label && (
                <div className={styles.label__container}>
                    <Label
                        weight="600"
                        className={styles.label}
                        {...labelProps}
                    >
                        {label}
                        {labelSuffix && (
                            <>
                                {/* Use an extra element as separator so everything stays inline and no extra margin on either side needed, which doesn't look right when text starts wrapping */}
                                <span
                                    className={
                                        styles['label__suffix-separator']
                                    }
                                ></span>
                                <Text
                                    variant="line"
                                    italic
                                    size="small"
                                    className={styles.label__suffix}
                                >
                                    {labelSuffix}
                                </Text>
                            </>
                        )}
                    </Label>
                    {tooltipTrigger}
                </div>
            )}

            <div className={baseClassList}>
                <Select
                    items={selectItems}
                    placeholder={selectPlaceholder}
                    defaultSelectedKey={selectDefaultSelectedKey}
                    name={selectName}
                    aria-labelledby={labelProps.id}
                    aria-describedby={descriptionProps.id}
                    setIsSelectFocused={setIsSelectFocused}
                >
                    {/* eslint-disable-next-line @typescript-eslint/no-explicit-any */}
                    {(item: any) => (
                        <Item key={item.code} textValue={item.code}>
                            <span className={styles['select-item']}>
                                {item.code} {item.country}
                            </span>
                        </Item>
                    )}
                </Select>

                <input
                    {...mergeProps(inputProps, textfieldFocusProps)}
                    className={styles.textfield}
                    ref={textfieldRef}
                    type="tel"
                />
            </div>

            {hasMessages && (
                <Stack>
                    {/* Show custom error message only when isInvalid */}
                    {isInvalid && errorMessage && (
                        <div
                            {...errorMessageProps}
                            className={styles.messages__error}
                        >
                            <Text variant="line" size="small">
                                {errorMessage}
                            </Text>
                        </div>
                    )}
                    {description && (
                        <div
                            {...descriptionProps}
                            className={descriptionClassList}
                        >
                            <Text variant="line" size="small">
                                {description}
                            </Text>
                        </div>
                    )}
                </Stack>
            )}
        </Stack>
    );
};

export default Phone;
