import React, {
    Dispatch,
    MutableRefObject,
    SetStateAction,
    useEffect,
} from 'react';
import {
    AriaButtonProps,
    mergeProps,
    useButton,
    useFocusRing,
} from 'react-aria';
import classNames from 'classnames';

import { Text } from '../Typography';

import styles from './Select.module.scss';

interface SelectButtonProps extends AriaButtonProps {
    buttonRef: MutableRefObject<HTMLButtonElement>;
    className: string;
    setIsSelectFocused: Dispatch<SetStateAction<boolean>>;
}

const SelectButton = (props: SelectButtonProps) => {
    const { buttonRef, children, className, setIsSelectFocused } = props;
    const { buttonProps } = useButton(props, buttonRef);
    const { isFocusVisible, focusProps } = useFocusRing();

    useEffect(() => {
        setIsSelectFocused(isFocusVisible);
    }, [isFocusVisible]);

    const classList = classNames(className, {
        [styles['select--focused']]: isFocusVisible,
    });

    return (
        <button
            {...mergeProps(buttonProps, focusProps)}
            className={classList}
            ref={buttonRef}
        >
            <Text variant="line" size="default" className={styles.select__text}>
                {children}
            </Text>
        </button>
    );
};

export default SelectButton;
