import React, {
    Dispatch,
    MutableRefObject,
    SetStateAction,
    useRef,
} from 'react';
import {
    AriaSelectOptions,
    HiddenSelect,
    mergeProps,
    useButton,
    useInteractOutside,
    useSelect,
} from 'react-aria';
import { useSelectState } from 'react-stately';
import type { CollectionChildren } from '@react-types/shared';
import classNames from 'classnames';

import SvgCaretDown from '../Icons/CaretDown';
import Popover from '../Popover/Popover';
import SelectListBox from '../SelectListBox/SelectListBox';

import SelectButton from './SelectButton';

import styles from './Select.module.scss';

export interface SelectProps extends AriaSelectOptions<object> {
    children?: CollectionChildren<object>;
    setIsSelectFocused: Dispatch<SetStateAction<boolean>>;
}

const Select = (props: SelectProps) => {
    const triggerRef = useRef<HTMLButtonElement>(null);
    const portalRef = useRef<HTMLDivElement>(null);

    const state = useSelectState(props);
    const { triggerProps, valueProps, menuProps } = useSelect(
        props,
        state,
        triggerRef
    );
    const { buttonProps } = useButton(props, triggerRef);

    const { isOpen, selectedItem } = state;
    const { label, name, placeholder, setIsSelectFocused } = props;

    useInteractOutside({
        ref: portalRef,
        onInteractOutside: state.close,
    });

    const classList = classNames(styles.select, {
        [styles['select--active']]: isOpen,
    });

    return (
        <div ref={portalRef} className={styles.base}>
            <HiddenSelect
                state={state}
                triggerRef={triggerRef}
                label={label}
                name={name}
            />

            <SelectButton
                {...mergeProps(triggerProps, buttonProps)}
                buttonRef={triggerRef as MutableRefObject<HTMLButtonElement>}
                className={classList}
                // Concat label with select value
                aria-labelledby={`${buttonProps['aria-labelledby']} ${valueProps.id}`}
                setIsSelectFocused={setIsSelectFocused}
            >
                <span {...valueProps}>
                    {selectedItem ? (
                        selectedItem.rendered
                    ) : (
                        <span className={styles.select__placeholder}>
                            {placeholder}
                        </span>
                    )}
                </span>
                <SvgCaretDown
                    className={styles.select__icon}
                    aria-hidden="true"
                />
            </SelectButton>

            {isOpen && (
                <Popover
                    state={state}
                    triggerRef={triggerRef}
                    portalContainer={portalRef.current}
                    offset={6}
                    className={styles.popover}
                    placement="bottom start"
                    isNonModal
                >
                    <SelectListBox {...menuProps} state={state} />
                </Popover>
            )}
        </div>
    );
};

Select.displayName = 'Select';

export default Select;
